.page {
  height: 100%;
  width: 100%;
  margin: 0 auto;
  scroll-behavior: smooth;
}

.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-height: 100%;
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 32px;
  font-weight: 400;
  letter-spacing: -0.01em;
  color: $basic-black;
  background-color: $basic-white;
}

.container {
  padding: 0 140px;
  margin: 0 auto;

  @media (max-width: 1023px) {
    padding: 0 20px;
  }

  @media (max-width: 767px) {
    padding: 0 16px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.invisible {
  display: none;
}

.not-available {
  filter: blur(2px);
  pointer-events: none;
}
